var dateType = "DD";
var GetTimerDashboard =[];
import MultiSelect from "primevue/multiselect";
import VueApexCharts from "vue-apexcharts";
import DataService from "../../services/DataService";
import ProgressBar from "vuejs-progress-bar";
import Treeselect from '@riophae/vue-treeselect'
// import moment from 'moment';
import TimetrackDashBoardVue from '../TimeTracking/TimetrackDashBoard.vue'
import TimerLoader from '../../components/Common/TimerProductiveLoader.vue'
// import the styles
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
  components: { VueApexCharts, MultiSelect, ProgressBar, Treeselect, TimetrackDashBoardVue, TimerLoader },
  data() {
    return {
      // define options
      value: [],
      checkedUserIds: [],
      multiselectoptions: [],
      departmentIds: null,
      User: "2",
      ddlUser: [
        {
          id: "1",
          Name: "My View",
        },
        {
          id: "2",
          Name: "Department View",
        },
      ],
      selecteddepartmets: "",
      //Start Productive bar line bar graph
      countries: [],
      options: {
       colors: ["#42c683", "#fcae18", "#c0c0c0", "#ededed"],
        chart: {
          type: 'bar',
          stacked: true,
          toolbar: {
            show: false
          },
          zoom: {
            enabled: true
          }
        },
        responsive: [{
          breakpoint: 480,
          options: {
            legend: {
              position: 'bottom',
              offsetX: -10,
              offsetY: 0
            }
          }
        }],
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: "100%"
          }
        },
        dataLabels: {
              enabled: false
            },
        stroke: {
          width: 1,
          colors: ["#fff"]
        },
        title: {
          text: "Productivity Bar",
          align: 'left',
  // margin: 0,
    offsetX: 0,
    offsetY: 30,
   // floating: false,
    // style: {
    //   fontSize:  '14px',
    //   fontWeight:  'bold',
    //   fontFamily:  undefined,
    //   color:  '#263238'
    // },
        },
        xaxis: {
          categories: [],
          labels: {
            formatter: function (value) {
              if (dateType == "DD") {
                if(value.includes(":00")>0)
                  {
                    value = value.replace(':00', '');
                    if (value[0] == '0') {
                      value = value.substring(1);
                    }
                  }
                  else
                  {
                  value="";
                  }
                return value;
              }
              else if (dateType == "WK") {
               // if (value == 'Monday 00:00:00' || value == 'Tuesday 00:00:00' || value == 'Wednesday 00:00:00' || value == 'Thursday 00:00:00' || value == 'Friday 00:00:00' || value == 'Saturday 00:00:00' || value == 'Sunday 00:00:00') 
                if(value.includes("00:00:00")>0)
                {
                  value = value.replace(/ 00:00:00/, '');
                }
                else {
                  value = "";
                }
                return value;
              }

              else {
                return value;
              }
            },
          },
          // group: {
          //   style: {
          //     fontSize: '10px',
          //     fontWeight: 700
          //   },
          //   groups: [
          //     { title: '12 AM', cols: 4 },
          //     { title: '1 AM', cols: 4 },
          //     { title: '2 AM', cols: 4 },
          //     { title: '3 AM', cols: 4 },
          //     { title: '4 AM', cols: 4 },
          //     { title: '5 AM', cols: 4 },
          //   ]
