    ConvertDateFormat(date) {
      var newDate = new Date(date);
      var mnth = ("0" + (newDate.getMonth() + 1)).slice(-2);
      var day = ("0" + newDate.getDate()).slice(-2);
      var datavalue = [newDate.getFullYear(), mnth, day].join("-");
      return datavalue;
    },
    selectToday: function (reqFrom) {
      var vm = this;
      vm.dateType = "DD";
