    ViewAllApps(apptype) {
      var vm = this;
      if (apptype == "productive") {
        vm.TempProductiveApps = vm.ProductiveApps;
        vm.isShowViewAllProductive = false;
      } else if (apptype == "unproductive") {
        vm.TempUnProductiveApps = vm.UnProductiveApps;
        vm.isShowViewAllUnProductive = false;
      }
      else if (apptype == "ProductiveEmpoyees") {
        vm.TempProductiveEmpoyees = vm.ProductiveEmpoyees;
        vm.isShowViewAllEmp = false;
      }
      else if (apptype == "UnProductiveEployees") {
        vm.TempProductiveUnemp = vm.UnProductiveEployees;
        vm.isShowViewAllUnEmp = false;
      }
      else {
        vm.TempNeutralApps = vm.NeutralApps;
        vm.isShowViewAllNeutral = false;
      }
