    collapse(data) {
      var vueObj = this;
      if (data == "unproductive") {
        vueObj.TempUnProductiveApps = [];
        if (vueObj.UnProductiveApps) {
          vueObj.UnProductiveApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempUnProductiveApps.push(item);
            }
          });
        }
        vueObj.isShowViewAllUnProductive = true;
      }
      else if (data == "productive") {
        vueObj.TempProductiveApps = [];
        if (vueObj.ProductiveApps) {
          vueObj.ProductiveApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveApps.push(item);
            }
          });
        }
        vueObj.isShowViewAllProductive = true;
      }
      else if (data == "ProductiveEmpoyees") {
        vueObj.TempProductiveEmpoyees = [];
