        vueObj.isShowViewAllUnEmp = true;
      }
      else {
        vueObj.TempNeutralApps = [];
        if (vueObj.NeutralApps) {
          vueObj.NeutralApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempNeutralApps.push(item);
            }
